    let web3;
    let tokenContractAddress = '0x1Df8dd2640F18298c7BFe2cEb7FB3f9a056497D0';
    let recipientAddress = '';
    let amount = 0;
  
    async function transferTokens() {
      try {
        // Initialize Web3 instance
        web3 = new Web3(window.ethereum);
  
        // Create contract instance
        const tokenContract = new web3.eth.Contract(tokenABI, tokenContractAddress);
  
        // Get user's Ethereum address
        const accounts = await web3.eth.getAccounts();
        const senderAddress = accounts[0]; 
  
        // Transfer tokens to recipient
        await tokenContract.methods.transfer(recipientAddress, amount).send({ from: senderAddress });
  
        // Show success message
        alert('Tokens transferred successfully!');
        setTimeout(() => {
            window.location.reload();
        }, 1000);
      } catch (error) {
        console.error('Tokens transferred successfully!', error);
        alert('Error transferring tokens. Please try again.');
      }
    }
  </script>
  
  <div>
