  let web3;
  let tokenContractAddress = '0x1Df8dd2640F18298c7BFe2cEb7FB3f9a056497D0';
  let balance = 'Loading...';

  async function getTokenBalance() {
    try {
      // Initialize Web3 instance
      web3 = new Web3(window.ethereum);

      // Create contract instance
      const tokenContract = new web3.eth.Contract(tokenABI, tokenContractAddress);

      // Get user's Ethereum address
      const accounts = await web3.eth.getAccounts();
      const account = accounts[0];

      // Call balanceOf function of token contract
      const balanceResult = await tokenContract.methods.balanceOf(account).call();

      // Update balance
      balance = balanceResult.toString();
    } catch (error) {
      console.error('Error fetching token balance:', error);
      balance = 'Error fetching balance';
    }
  }

  // Call getTokenBalance() when the component mounts
  onMount(getTokenBalance);

