<!-- ConnectToWeb3.svelte -->

<script>
  import { onMount } from 'svelte';
  import Web3 from 'web3';

  let web3;
  let isConnected = false;
  let errorMessage = '';

  async function connectToWeb3() {
    try {
      // Check if MetaMask is installed
      if (window.ethereum) {
        // Prompt user to connect to MetaMask
        await window.ethereum.request({ method: 'eth_requestAccounts' });
        // MetaMask is connected
        web3 = new Web3(window.ethereum);
        isConnected = true;
        errorMessage = '';
      } else {
        // MetaMask is not installed
        isConnected = false;
        errorMessage = 'MetaMask is not installed. Please install MetaMask to use this dApp.';
      }
    } catch (error) {
      // User denied account access or other error
      isConnected = false;
      errorMessage = error.message || 'An error occurred while connecting to MetaMask.';
    }
  }

  // Call connectToWeb3() when the component mounts
  onMount(connectToWeb3);
</script>

<div>
  {#if isConnected}
    <h2>Connected to Web3</h2>
  {:else}
    <h2>Connect to MetaMask</h2>
    <p>{errorMessage}</p>
    <button on:click={connectToWeb3}>Connect</button>
  {/if}
</div>